'use client';

import React, { useState, useEffect, useRef, useMemo } from 'react';
import { Search, Navigation, Menu, X, ChevronRight, MapPin, Sparkles, Trophy, Star, Award, Dices, TrendingUp, Bike, Ship, Flag, PhoneCall, Map as MapIcon, Filter, LocateFixed, AlertTriangle, QrCode, BarChart3, ListOrdered, Info, ExternalLink, CheckCircle2, FileText, PieChart, Zap, Timer, Flame, CloudMoon, Moon, MessageCircle, Download, ShieldCheck, Wallet, Newspaper, HelpCircle, ArrowUpRight, ZapOff, Activity, LayoutDashboard, Share2, Binary, Database } from 'lucide-react';
import { LotteryStore, StoreType, LuckAnalysis, DreamInterpretation, TabType } from '../../lib/types';
import { analyzeLuckLocal } from '../../lib/luckEngine';
import { MOCK_STORES } from '../../lib/data';
import DREAM_DATA from '../../data/dream_mapping.json';
import KNOWLEDGE_DATA from '../../data/lotto_knowledge.json';
import EXPERT_REPORT from '../../data/expert_report.json';

import { Chart as ChartJS, CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend, ArcElement, PointElement, LineElement, Filler } from 'chart.js';
import { Bar, Pie, Line } from 'react-chartjs-2';

ChartJS.register(CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend, ArcElement, PointElement, LineElement, Filler);

declare global { interface Window { kakao: any; } }

const getDistance = (lat1: number, lon1: number, lat2: number, lon2: number) => {
    const R = 6371e3;
    const a = Math.sin((lat2 - lat1) * Math.PI / 360) ** 2 + Math.cos(lat1 * Math.PI / 180) * Math.cos(lat2 * Math.PI / 180) * Math.sin((lon2 - lon1) * Math.PI / 360) ** 2;
    return R * (2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a)));
};

export default function MapInterface() {
  const [activeTab, setActiveTab] = useState<TabType>('MAP');
  const [userLocation, setUserLocation] = useState<{lat: number, lng: number} | null>(null);
  const [selectedStore, setSelectedStore] = useState<LotteryStore | null>(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [dreamSearch, setDreamSearch] = useState('');
  const [sortBy, setSortBy] = useState<'WIN' | 'DISTANCE'>('WIN');
  const [isSidebarOpen, setIsSidebarOpen] = useState(true);
  const [analysis, setAnalysis] = useState<LuckAnalysis | null>(null);
  const [isAnalyzing, setIsAnalyzing] = useState(false);
  
  const mapRef = useRef<any>(null);
  const markersRef = useRef<{ [key: string]: any }>({});

  useEffect(() => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (pos) => setUserLocation({ lat: pos.coords.latitude, lng: pos.coords.longitude }),
        null, { enableHighAccuracy: true }
      );
    }
  }, []);

  const processedStores = useMemo(() => {
    return MOCK_STORES.map(s => ({
        ...s,
        luckIndex: 65 + (s.winCount1st * 4) + Math.floor(Math.random() * 8),
        distance: userLocation ? getDistance(userLocation.lat, userLocation.lng, s.lat, s.lng) : undefined,
    })).filter(s => s.name.includes(searchTerm) || s.address.includes(searchTerm))
    .sort((a, b) => {
      if (sortBy === 'DISTANCE' && a.distance && b.distance) return a.distance - b.distance;
      return b.winCount1st - a.winCount1st;
    });
  }, [searchTerm, sortBy, userLocation]);

  const filteredDreams = useMemo(() => {
    return (DREAM_DATA as DreamInterpretation[]).filter(d => d.keyword.includes(dreamSearch));
  }, [dreamSearch]);

  useEffect(() => {
    if (activeTab === 'MAP' && typeof window !== 'undefined' && window.kakao && window.kakao.maps) {
        window.kakao.maps.load(() => {
            const container = document.getElementById('map-container');
            if (container && !mapRef.current) {
                mapRef.current = new window.kakao.maps.Map(container, { 
                    center: new window.kakao.maps.LatLng(userLocation?.lat || 37.5665, userLocation?.lng || 126.9780), 
                    level: 8 
                });
            }
        });
    }
  }, [activeTab, userLocation]);

  const frequencyData = {
    labels: ['34', '18', '27', '1', '43', '12', '5', '39'],
    datasets: [{ label: 'Frequency', data: [152, 148, 145, 142, 140, 138, 135, 132], backgroundColor: '#4F46E5', borderRadius: 10 }]
  };

  const openNaverMap = (store: LotteryStore) => {
    window.open(`https://map.naver.com/v5/search/${encodeURIComponent(store.address)}`, 'naver_map_window');
  };

  const handleAnalyze = async () => {
    if (!selectedStore) return;
    setIsAnalyzing(true);
    setTimeout(() => {
      setAnalysis(analyzeLuckLocal(selectedStore));
      setIsAnalyzing(false);
    }, 1000);
  };

  useEffect(() => {
    if (activeTab !== 'MAP' || !mapRef.current) return;
    
    window.kakao.maps.load(() => {
        Object.values(markersRef.current).forEach(m => m.setMap(null));
        markersRef.current = {};
        processedStores.forEach(store => {
          const marker = new window.kakao.maps.Marker({
            position: new window.kakao.maps.LatLng(store.lat, store.lng),
            map: mapRef.current,
          });
          window.kakao.maps.event.addListener(marker, 'click', () => setSelectedStore(store as any));
          markersRef.current[store.id] = marker;
        });
    });
  }, [processedStores, activeTab]);

  return (
    <div className="flex flex-col h-screen bg-slate-50 font-sans text-slate-900 overflow-hidden antialiased">
      <nav className="bg-white border-b px-6 flex justify-between items-center h-20 shrink-0 z-50 shadow-sm">
        <div className="flex items-center space-x-3">
            <div className="bg-indigo-600 p-2.5 rounded-2xl shadow-lg shadow-indigo-100"><Activity className="text-white w-6 h-6" /></div>
            <div>
                <h1 className="text-xl font-black tracking-tighter">LottoInsights <span className="bg-red-500 text-white text-[8px] px-1.5 py-0.5 rounded-full ml-1 animate-pulse">LIVE</span></h1>
                <p className="text-[9px] text-slate-400 font-bold uppercase tracking-widest leading-none mt-1">Data-Driven Engine</p>
            </div>
        </div>
        <div className="flex space-x-1 overflow-x-auto scrollbar-hide">
            {[
                { id: 'MAP', icon: MapIcon, label: '지도' },
                { id: 'RANK', icon: ListOrdered, label: '순위' },
                { id: 'REPORT', icon: FileText, label: '분석' },
                { id: 'DREAM', icon: Moon, label: '꿈해몽' },
                { id: 'HELP', icon: HelpCircle, label: '정보' },
            ].map(tab => (
                <button key={tab.id} onClick={() => { setActiveTab(tab.id as TabType); setSelectedStore(null); }} className={`px-4 py-2 rounded-2xl flex items-center space-x-2 transition-all ${activeTab === tab.id ? 'bg-indigo-600 text-white shadow-xl shadow-indigo-100 scale-105' : 'text-slate-400 hover:bg-slate-50'}`}>
                    <tab.icon className="w-4 h-4" />
                    <span className="text-xs font-bold whitespace-nowrap">{tab.label}</span>
                </button>
            ))}
        </div>
      </nav>

      <div className="flex-1 flex overflow-hidden relative">
        <main className="flex-1 relative h-full">
            {activeTab === 'MAP' && (
                <div className="h-full w-full relative">
                    <div id="map-container" className="h-full w-full bg-slate-200"></div>
                    {!selectedStore && (
                        <aside className={`absolute top-4 left-4 z-20 h-[calc(100%-2rem)] bg-white/95 backdrop-blur-xl border border-slate-100 rounded-[40px] shadow-2xl transition-all duration-500 ${isSidebarOpen ? 'w-full md:w-[400px]' : 'w-0 overflow-hidden'}`}>
                           <div className="flex flex-col h-full p-8">
                              <div className="space-y-6 mb-8">
                                <div className="flex justify-between items-center"><h2 className="text-2xl font-black tracking-tight text-slate-900">명당 탐색</h2><button onClick={() => setIsSidebarOpen(false)} className="p-2 hover:bg-slate-100 rounded-full transition-all"><X className="w-6 h-6" /></button></div>
                                <div className="relative"><Search className="absolute left-5 top-4.5 text-slate-400 w-5 h-5" /><input type="text" placeholder="지역, 가게명 검색..." className="w-full pl-14 pr-6 py-5 bg-slate-50 border-none rounded-[24px] text-sm focus:ring-2 focus:ring-indigo-500 font-bold" value={searchTerm} onChange={(e) => setSearchTerm(e.target.value)} /></div>
                              </div>
                              <div className="flex-1 overflow-y-auto space-y-4 pr-2 scrollbar-hide pb-10">
                                 {processedStores.map(s => (
                                    <div key={`${s.id}-${s.address}`} onClick={() => setSelectedStore(s as any)} className="p-6 rounded-[36px] bg-white border border-slate-100 hover:border-indigo-200 hover:shadow-2xl hover:shadow-indigo-100/50 cursor-pointer transition-all flex items-center space-x-4 group">
                                        <div className="flex-1 min-w-0">
                                            <h3 className="font-black text-base group-hover:text-indigo-600 truncate mb-1">{s.name}</h3>
                                            <p className="text-xs text-slate-400 truncate font-medium">{s.address}</p>
                                            <div className="flex items-center space-x-2 mt-4">
                                                <span className="text-[10px] font-black text-red-500 bg-red-50 px-3 py-1 rounded-full border border-red-100">1등 {s.winCount1st}회</span>
                                                <span className="text-[10px] font-black text-indigo-600 bg-indigo-50 px-3 py-1 rounded-full border border-indigo-100">지수 {(s as any).luckIndex}</span>
                                            </div>
                                        </div>
                                        <div className="bg-slate-50 p-2 rounded-2xl group-hover:bg-indigo-50 transition-colors"><ChevronRight className="w-5 h-5 text-slate-300 group-hover:text-indigo-600" /></div>
                                    </div>
                                 ))}
                              </div>
                           </div>
                        </aside>
                    )}
                </div>
            )}

            {activeTab === 'RANK' && (
                <div className="h-full overflow-y-auto p-10 bg-white animate-in fade-in duration-500">
                    <div className="max-w-3xl mx-auto space-y-4 pb-20">
                        <div className="flex justify-between items-end mb-12"><h2 className="text-4xl font-black tracking-tighter">당첨 명당 순위</h2><p className="text-xs font-black text-indigo-600 tracking-[0.3em] uppercase border-b-2 border-indigo-600 pb-1">Top 50 Hotspots</p></div>
                        {processedStores.sort((a,b) => b.winCount1st - a.winCount1st).slice(0, 50).map((s, idx) => (
                            <div key={`rank-${s.id}-${s.address}`} onClick={() => { setSelectedStore(s as any); setActiveTab('MAP'); }} className="flex items-center p-6 bg-slate-50 rounded-[40px] border border-slate-100 hover:border-indigo-200 hover:bg-white hover:shadow-2xl transition-all cursor-pointer group">
                                <div className={`w-14 h-14 flex items-center justify-center rounded-[20px] font-black mr-6 shrink-0 text-xl ${idx < 3 ? 'bg-yellow-400 text-white shadow-xl shadow-yellow-100' : 'bg-white text-slate-300 border'}`}>{idx + 1}</div>
                                <div className="flex-1 min-w-0"><h3 className="font-black truncate text-lg group-hover:text-indigo-600">{s.name}</h3><p className="text-xs text-slate-400 truncate font-bold mt-1">{s.address}</p></div>
                                <div className="text-right ml-6"><p className="text-3xl font-black text-red-500 tracking-tighter">{s.winCount1st}<span className="text-sm ml-1 opacity-40">회</span></p></div>
                            </div>
                        ))}
                    </div>
                </div>
            )}

            {/* 📊 분석(REPORT) 탭: Raw Data 테이블 + 전문가 추천 조합 게시 */}
            {activeTab === 'REPORT' && (
                <div className="h-full overflow-y-auto p-12 bg-slate-50 animate-in slide-in-from-right duration-700">
                    <div className="max-w-5xl mx-auto space-y-12 pb-20">
                        <header className="flex justify-between items-center">
                            <div className="space-y-2">
                                <h2 className="text-5xl font-black tracking-tighter text-slate-900 flex items-center"><Database className="mr-4 text-indigo-600 w-12 h-12" /> Raw Data Analysis</h2>
                                <p className="text-xl text-slate-500 font-bold">로또플레이 25,000행 기반 지능형 추출 엔진</p>
                            </div>
                            <div className="bg-indigo-600 text-white px-6 py-3 rounded-3xl font-black shadow-xl shadow-indigo-200 animate-pulse">NEW COMBINATIONS</div>
                        </header>

                        {/* 1. 전문가 추천 조합 섹션 */}
                        <section className="bg-slate-900 rounded-[60px] p-12 text-white shadow-2xl relative overflow-hidden">
                            <div className="absolute top-0 right-0 p-10 opacity-10"><Zap className="w-64 h-64" /></div>
                            <h3 className="text-2xl font-black mb-10 flex items-center text-indigo-300"><Sparkles className="mr-3" /> 데이터 기반 추천 조합 (5세트)</h3>
                            <div className="grid grid-cols-1 md:grid-cols-5 gap-4">
                                {EXPERT_REPORT.combinations.map((combo: any) => (
                                    <div key={combo.id} className="bg-white/10 border border-white/10 rounded-[40px] p-6 hover:bg-white/20 transition-all text-center">
                                        <p className="text-[10px] font-black text-slate-400 mb-4 uppercase tracking-[0.2em]">Combo #{combo.id}</p>
                                        <div className="grid grid-cols-2 gap-2 mb-6">
                                            {combo.numbers.map((n: number) => (
                                                <div key={n} className="w-8 h-8 flex items-center justify-center bg-indigo-600 rounded-full text-[10px] font-black shadow-lg">{n}</div>
                                            ))}
                                        </div>
                                        <div className="space-y-1">
                                            <p className="text-[10px] font-bold text-indigo-300">합계: {combo.sum}</p>
                                            <p className="text-[10px] font-bold text-indigo-300">패턴: {combo.pattern}</p>
                                        </div>
                                    </div>
                                ))}
                            </div>
                        </section>

                        {/* 2. Raw Data 테이블 섹션 */}
                        <section className="bg-white rounded-[60px] p-12 shadow-sm border border-slate-100">
                            <h3 className="text-2xl font-black mb-8 flex items-center text-slate-900"><Binary className="mr-3 text-indigo-600" /> 출현 빈도 Raw Data (Top 10)</h3>
                            <div className="overflow-hidden rounded-[32px] border border-slate-100">
                                <table className="w-full text-left border-collapse">
                                    <thead>
                                        <tr className="bg-slate-50 border-b border-slate-100">
                                            <th className="p-6 font-black text-xs text-slate-400 uppercase tracking-widest">Rank</th>
                                            <th className="p-6 font-black text-xs text-slate-400 uppercase tracking-widest">Number</th>
                                            <th className="p-6 font-black text-xs text-slate-400 uppercase tracking-widest">Frequency</th>
                                            <th className="p-6 font-black text-xs text-slate-400 uppercase tracking-widest">Status</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {EXPERT_REPORT.rawTable.map((row: any) => (
                                            <tr key={row.number} className="border-b border-slate-50 hover:bg-slate-50/50 transition-colors">
                                                <td className="p-6 font-black text-slate-400">#{row.rank}</td>
                                                <td className="p-6"><div className="w-10 h-10 flex items-center justify-center bg-slate-900 text-white rounded-2xl font-black">{row.number}</div></td>
                                                <td className="p-6 font-black text-lg text-slate-900">{row.frequency}회</td>
                                                <td className="p-6"><span className="px-3 py-1 bg-red-100 text-red-600 text-[10px] font-black rounded-full uppercase tracking-tighter">{row.status}</span></td>
                                            </tr>
                                        ))}
                                    </tbody>
                                </table>
                            </div>
                        </section>
                    </div>
                </div>
            )}

            {/* 나머지 탭 로직 동일 */}
            {activeTab === 'STATS' && (
                <div className="h-full overflow-y-auto p-12 bg-slate-50 animate-in fade-in duration-700">
                    <div className="max-w-6xl mx-auto space-y-12">
                        <h2 className="text-4xl font-black tracking-tighter">전략 대시보드</h2>
                        <div className="bg-white p-12 rounded-[60px] shadow-sm border border-slate-100 h-[400px]"><Bar data={frequencyData} options={{ maintainAspectRatio: false, plugins: { legend: { display: false } } }} /></div>
                    </div>
                </div>
            )}

            {activeTab === 'DREAM' && (
                <div className="h-full overflow-y-auto p-12 bg-slate-900 text-white animate-in slide-in-from-right duration-500">
                    <div className="max-w-2xl mx-auto space-y-12 py-10">
                        <header className="text-center space-y-6"><div className="w-24 h-24 bg-white/5 rounded-[40px] flex items-center justify-center mx-auto border border-white/10 shadow-2xl shadow-indigo-500/10"><Moon className="w-12 h-12 text-yellow-400 animate-pulse" /></div><div className="space-y-2"><h2 className="text-5xl font-black tracking-tighter uppercase italic text-yellow-400">Dream Decoder</h2><p className="text-slate-400 font-bold text-lg">무의식 속의 당첨 시그널을 해석합니다.</p></div></header>
                        <div className="relative"><Search className="absolute left-8 top-7 text-slate-600 w-7 h-7" /><input type="text" placeholder="꿈 키워드 입력..." className="w-full pl-20 pr-8 py-7 bg-white/5 border border-white/10 rounded-[48px] text-2xl focus:ring-4 focus:ring-indigo-500/30 font-black outline-none transition-all" value={dreamSearch} onChange={(e) => setDreamSearch(e.target.value)} /></div>
                        <div className="grid grid-cols-1 gap-8">{filteredDreams.map((d, i) => (<div key={`dream-${i}`} className="p-10 bg-white/5 border border-white/5 rounded-[60px] border-l-[12px] border-l-yellow-400 shadow-2xl"><div className="flex justify-between items-start mb-8"><h3 className="text-3xl font-black text-yellow-400">#{d.keyword}</h3><div className="flex space-x-3">{d.numbers.map(n => (<span key={`${d.keyword}-${n}`} className="w-12 h-12 flex items-center justify-center bg-indigo-600 rounded-full text-base font-black shadow-2xl shadow-indigo-900">{n}</span>))}</div></div><p className="text-xl text-slate-300 leading-relaxed font-medium italic">"{d.meaning}"</p></div>))}</div>
                    </div>
                </div>
            )}

            {activeTab === 'HELP' && (
                <div className="h-full overflow-y-auto p-12 bg-white animate-in slide-in-from-right duration-700">
                    <div className="max-w-5xl mx-auto space-y-16">
                        <header className="flex justify-between items-center"><h2 className="text-4xl font-black tracking-tighter flex items-center text-slate-900"><Newspaper className="mr-4 text-indigo-600 w-10 h-10" /> 로또 지식 백과</h2></header>
                        <div className="grid grid-cols-1 md:grid-cols-2 gap-10">
                            <section className="bg-indigo-50 p-12 rounded-[60px] border border-indigo-100 shadow-sm relative overflow-hidden"><h3 className="font-black text-2xl mb-10 flex items-center text-indigo-900">당첨금 수령 프로세스</h3><ul className="space-y-8 text-indigo-900/70 font-black">{['1등: 농협은행 본점 (서울 서대문)', '2/3등: 농협은행 전국 각 지점', '4/5등: 로또 판매점 및 편의점'].map((text, i) => (<li key={i} className="flex items-center text-lg"><ArrowUpRight className="w-6 h-6 mr-4 text-indigo-600" /> {text}</li>))}</ul></section>
                            <section className="bg-slate-50 p-12 rounded-[60px] border border-slate-100 shadow-sm relative overflow-hidden"><h3 className="font-black text-2xl mb-10 flex items-center text-slate-900">구매 시 필수 주의사항</h3><ul className="space-y-8 text-slate-600 font-black">{['판매시간: 06:00 ~ 24:00 (매일)', '토요일 판매 마감: 20:00 (추첨일)', '구매한도: 1인 1회 10만원 한도'].map((text, i) => (<li key={i} className="flex items-center text-lg"><CheckCircle2 className="w-6 h-6 mr-4 text-green-500" /> {text}</li>))}</ul></section>
                        </div>
                        <section className="bg-white border-4 border-dashed border-slate-100 p-12 rounded-[60px]"><h3 className="font-black text-2xl mb-10 text-slate-900">전문가 구입 요령 (FAQ)</h3><div className="grid grid-cols-1 gap-10">{KNOWLEDGE_DATA.slice(0, 4).map((item: any) => (<div key={item.id} className="space-y-4 group cursor-help"><h4 className="text-xl font-black text-slate-900 group-hover:text-indigo-600 transition-colors flex items-center"><Zap className="w-5 h-5 mr-3 text-yellow-500" /> {item.title}</h4><p className="text-lg text-slate-500 leading-relaxed font-medium">{item.content}</p></div>))}</div></section>
                    </div>
                </div>
            )}
        </main>

        {selectedStore && (
            <aside className="absolute inset-0 md:relative md:w-[500px] bg-white z-[60] animate-in slide-in-from-right duration-300 shadow-2xl flex flex-col border-l">
                <div className="p-8 border-b flex items-center justify-between sticky top-0 bg-white/90 backdrop-blur-md z-10"><button onClick={() => setSelectedStore(null)} className="p-4 hover:bg-slate-100 rounded-[24px] transition-all active:scale-90"><X className="w-8 h-8" /></button><h3 className="font-black text-[10px] tracking-[0.4em] uppercase text-slate-300">Intelligence Profile</h3><button onClick={() => alert('링크가 복사되었습니다.')} className="p-4 hover:bg-slate-100 rounded-[24px] text-indigo-600 transition-all"><Share2 className="w-6 h-6" /></button></div>
                <div className="p-10 overflow-y-auto flex-1 space-y-10 scrollbar-hide pb-20">
                    <section className="space-y-6"><div className="flex items-center space-x-2"><span className="px-4 py-1.5 bg-red-500 text-white text-[10px] font-black rounded-full shadow-2xl shadow-red-200">ELITE HOTSPOT</span><span className="px-4 py-1.5 bg-indigo-50 text-indigo-600 text-[10px] font-black rounded-full border border-indigo-100 flex items-center"><Zap className="w-3 h-3 mr-1.5" /> LUCK IDX {(selectedStore as any).luckIndex}</span></div><h2 className="text-5xl font-black text-slate-900 leading-[1.1] tracking-tighter">{selectedStore.name}</h2><p className="text-lg text-slate-400 font-bold leading-relaxed flex items-start"><MapPin className="w-6 h-6 mr-3 shrink-0 text-indigo-500 mt-1" />{selectedStore.address}</p></section>
                    <div className="bg-gradient-to-br from-orange-400 to-red-600 rounded-[50px] p-10 text-white shadow-2xl relative overflow-hidden group"><div className="absolute top-0 right-0 p-6 opacity-10 group-hover:scale-125 transition-transform duration-1000"><Flame className="w-32 h-32" /></div><h3 className="font-black text-2xl mb-8 flex items-center">스피또 실시간 1등 재고</h3><div className="grid grid-cols-3 gap-4">{[ {type:'2000', l:2}, {type:'1000', l:5}, {type:'500', l:1} ].map(sp => (<div key={sp.type} className="bg-white/10 rounded-[32px] p-6 text-center border border-white/10 backdrop-blur-md hover:bg-white/20 transition-all"><p className="text-[11px] font-black opacity-60 mb-2">{sp.type}</p><p className="text-3xl font-black">{sp.l}<span className="text-sm ml-1">매</span></p></div>))}</div><p className="text-[10px] mt-8 text-center font-black opacity-40 uppercase tracking-widest italic">Live Inventory Tracking Active</p></div>
                    <div className="grid grid-cols-1 gap-5"><button onClick={() => openNaverMap(selectedStore)} className="flex items-center justify-between p-7 bg-slate-900 text-white rounded-[40px] hover:shadow-2xl hover:shadow-indigo-200 transition-all group font-black text-lg"><div className="flex items-center"><MapIcon className="w-7 h-7 mr-5 text-green-400 transition-transform group-hover:scale-110" /> 네이버 길찾기</div><ArrowUpRight className="w-6 h-6 opacity-30 group-hover:opacity-100 transition-opacity" /></button></div>
                    <div className="bg-gradient-to-br from-indigo-600 to-violet-800 rounded-[60px] p-12 text-white relative overflow-hidden shadow-2xl shadow-indigo-200">
                        <div className="absolute top-0 left-0 w-full h-full bg-[url('https://www.transparenttextures.com/patterns/cubes.png')] opacity-10"></div>
                        {!analysis ? (
                            <div className="text-center relative z-10 space-y-8 py-6"><div className="w-24 h-24 bg-white/10 rounded-[40px] flex items-center justify-center mx-auto border border-white/10 backdrop-blur-md"><Sparkles className="w-12 h-12 text-yellow-300 animate-bounce" /></div><div className="space-y-2"><h3 className="font-black text-3xl tracking-tighter">AI 행운 분석</h3><p className="text-indigo-100 text-sm opacity-70 leading-relaxed font-bold">이 매장의 지형적 기운과 공공데이터<br/>당첨 패턴을 정밀 분석합니다.</p></div><button onClick={() => { handleAnalyze(); }} disabled={isAnalyzing} className="w-full bg-white text-indigo-600 font-black py-6 rounded-[32px] shadow-2xl active:scale-95 transition-all text-xl">{isAnalyzing ? "Analyzing Core..." : "분석 엔진 가동"}</button></div>
                        ) : (
                            <div className="space-y-10 relative z-10 animate-in zoom-in duration-700">
                                <div><p className="text-indigo-200 text-[11px] font-black uppercase tracking-[0.4em] mb-3">Algorithm Score</p><p className="text-8xl font-black text-yellow-300 tracking-tighter">{analysis.score}<span className="text-3xl text-white/30 ml-2">/100</span></p></div>
                                <p className="text-2xl font-bold leading-tight italic text-indigo-50">"{analysis.insights}"</p>
                                <div className="bg-white/10 rounded-[48px] p-10 backdrop-blur-xl border border-white/10 shadow-inner">
                                    <p className="text-[11px] text-indigo-200 font-black mb-8 uppercase text-center tracking-[0.5em]">AI Suggested Sequence</p>
                                    <div className="flex justify-between">{analysis.luckyNumber.map(n => (<span key={n} className="w-12 h-12 flex items-center justify-center bg-white text-indigo-900 rounded-full font-black text-lg shadow-2xl shadow-indigo-900/50 transform hover:scale-125 transition-all hover:rotate-12 cursor-pointer">{n}</span>))}</div>
                                </div>
                                <div className="flex space-x-4"><button onClick={() => alert('카톡 행운 카드 전송')} className="flex-1 bg-yellow-400 text-yellow-900 font-black py-5 rounded-[28px] text-[10px] flex flex-col items-center justify-center shadow-2xl active:scale-90 transition-all"><MessageCircle className="w-6 h-6 mb-2" /> 카톡 전달</button><button onClick={() => alert('고화질 이미지 저장')} className="flex-1 bg-white/10 text-white font-black py-5 rounded-[28px] text-[10px] flex flex-col items-center justify-center border border-white/20 hover:bg-white/20 transition-all active:scale-90"><Download className="w-6 h-6 mb-2" /> 이미지 저장</button></div>
                            </div>
                        )}
                    </div>
                </div>
            </aside>
        )}
      </div>

      <div className="absolute bottom-10 right-10 z-50 flex flex-col space-y-5">
          <button onClick={() => setActiveTab('HELP')} className="bg-white w-20 h-20 rounded-[32px] shadow-2xl flex items-center justify-center text-indigo-600 border border-slate-100 transition-all hover:scale-110 hover:rotate-12 active:scale-90"><Info className="w-10 h-10" /></button>
          {activeTab === 'MAP' && (
              <button onClick={() => setIsSidebarOpen(!isSidebarOpen)} className="md:hidden bg-indigo-600 w-20 h-20 rounded-[32px] shadow-2xl flex items-center justify-center text-white transition-all hover:scale-110 active:scale-90">{isSidebarOpen ? <X className="w-10 h-10" /> : <Search className="w-10 h-10" />}</button>
          )}
      </div>
    </div>
  );
}